.system-nav, .system-nav ul, .system-nav li {
    list-style: none;
}

.system-nav ul {
    padding: 0;
    margin: 0 0 0 18px;
}

.system-nav {
    padding: 4px;
    margin: 0px;
}

.system-nav > li {
    margin: 4px 0;
}

.system-nav > li li {
    margin: 2px 0;
}

.system-nav a {
    color: #333;
    display: block;
    outline: none;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    text-decoration: none;
}

.system-nav li > a > span {
    float: right;
    font-size: 19px;
    font-weight: bolder;
}

.system-nav li > a:hover > span {
    color: #fff;
}

.system-nav li > a > span:after {
    content: '\25be';
}
.system-nav li.open > a > span:after {
    content: '\25b4';
}

.system-nav a:hover, .system-nav li.active > a {
    background-color: #5D5D5D;
    color: #f5f5f5;
}

.system-nav > li.active > a  {
    background-color: #4D90FE;
}

.system-nav li a {
    font-size: 12px;
    line-height: 18px;
    padding: 2px 10px;
}

.system-nav > li > a {
    font-size: 14px;
    line-height: 20px;
    padding: 4px 10px;
}