@media only screen and (max-width: 767px) {
	.btn-toolbar .pull-right{
		float: left !important;
	}

	#display-all{
		display: none;
	}

	/* Responsividade para tabelas */
	/* No More Tables */
	/* Force table to not be like tables anymore */
	.table-serialized table, 
	.table-serialized thead, 
	.table-serialized tbody, 
	.table-serialized th, 
	.table-serialized td, 
	.table-serialized tr { 
		display: block; 
	}
 
	.table-serialized tbody tr th[scope=row]{
		background-color: #ccc;
	}
 
	/* Hide table headers (but not display: none;, for accessibility) */
	.table-serialized thead tr { 
		position: absolute;
		top: -9999px;
		left: -9999px;
	}
 
	.table-serialized tr { border: 1px solid #ccc; }
 
	.table-serialized tbody tr td,
	.table-serialized tbody tr th { 
		/* Behave  like a "row" */
		border: none;
		border-bottom: 1px solid #eee; 
		position: relative;
		padding-left: 40%; 
		white-space: normal;
		text-align:left;
	}

	.table-serialized .dataTables_empty{ 
		/* Behave  like a "row" */
		padding-left: 6px; 
	}	
	
	.table-serialized td:before, .table-serialized tbody th:before { 
		/* Now like a table header */
		position: absolute;
		/* Top/left values mimic padding */
		top: 6px;
		left: 6px;
		width: 45%; 
		padding-right: 10px; 
		/* white-space: nowrap; */
		text-align:left;
		font-weight: bold;
	}
 
	/*
	Label the data
	*/
	.table-serialized td:before { content: attr(data-title); }
	.table-serialized tbody th:before { content: attr(data-title); }

	/* Flip Scroll */	
	#flip-scroll .cf:after { visibility: hidden; display: block; font-size: 0; content: " "; clear: both; height: 0; }
	#flip-scroll * html .cf { zoom: 1; }
	#flip-scroll *:first-child+html .cf { zoom: 1; }
	
	#flip-scroll table { width: 100%; border-collapse: collapse; border-spacing: 0; }
 
	#flip-scroll th,
	#flip-scroll td { margin: 0; vertical-align: top; }
	#flip-scroll th { text-align: left; }
	
	#flip-scroll table { display: block; position: relative; width: 100%; }
	#flip-scroll thead { display: block; float: left; }
	#flip-scroll tbody { display: block; width: auto; position: relative; overflow-x: auto; overflow-y: hidden; white-space: nowrap; }
	#flip-scroll thead tr { display: block; }
	#flip-scroll th { display: block; text-align: right; }
	#flip-scroll tbody tr { display: inline-block; vertical-align: top; margin: -3px !important;}
	#flip-scroll td { display: block; min-height: 1.25em; text-align: left; }
 
 
	/* sort out borders */
 
	#flip-scroll th { border-bottom: 0; border-left: 0; }
	#flip-scroll td { border-left: 0; border-right: 0; border-bottom: 0; }
	#flip-scroll tbody tr { border-left: 1px solid #babcbf; }
	/* #flip-scroll th:last-child,
		#flip-scroll td:last-child { border-bottom: 1px solid #babcbf; }
	}	 */
}
